<template>
	<view class="pages-layout">
		<CustomNavbar :navbarHeight="navbarHeight" :systemHeight="systemHeight" isBack="true"/>
		<view class="page-wrapper" :style="{paddingTop:`${navbarHeight+systemHeight}px` , minHeight: `calc(100vh - ${navbarHeight}px)`}">
			<slot></slot>
		</view>
	</view>
</template>

<script setup>
	import CustomNavbar from '@/components/customNavbar.vue'
	import {
		ref,
		onMounted
	} from 'vue'
	import {
		getNavigationBarHeight
	} from '@/utils/index.js'
	import {
		onLoad
	} from '@dcloudio/uni-app'

	let navbarHeight = ref(45);
	let systemHeight = ref(40);

	onLoad(() => {
		const {
			statusBarHeight,
			navigationBarHeight
		} = getNavigationBarHeight();

		if (statusBarHeight || navigationBarHeight) {
			navbarHeight.value = navigationBarHeight
			systemHeight.value = statusBarHeight
		}
	})
</script>

<style scoped>
	.pages-layout {
		width: 100%;
		height: 100vh;
	}

	.page-wrapper {}
</style>